import React from 'react';
import Image from 'react-bootstrap/Image';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

class Home extends React.Component {
    render() {
        return (
            <div>
                <Container>
                    <Row>
                        <Col>
                            <h1>Home</h1>
                        </Col>
                        <Col>
                            <Image src="https://static.wikia.nocookie.net/animalcrossing/images/1/1b/Blathers_NH.png/revision/latest/scale-to-width-down/350?cb=20200229053519" />
                        </Col>
                    </Row>
                    <Row><Col>Hello! Welcome to my museum.</Col></Row>
                </Container>

            </div>
        )
    }
}

export default Home;