import React from 'react';
import './App.css';
import { HashRouter, Route } from 'react-router-dom';
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';

import Home from './components/home-component/Home';
import About from './components/about-component/About';
import Fish from './components/fish-component/Fish';

function App() {
  return (
    <HashRouter basename="/">
      <div>
        <Navbar bg="dark" expand="lg" variant="dark">
        <Navbar.Toggle aria-controls="basic-Navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="mr-auto">
            <Nav.Link href="#home">Home</Nav.Link>
            <Nav.Link href="#about">About</Nav.Link>
            <Nav.Link href="#fish">Fish</Nav.Link>
            <Nav.Link href="#insects">Insects</Nav.Link>
            <Nav.Link href="#fossils">Fossils</Nav.Link>
            <Nav.Link href="#art">Art</Nav.Link>
          </Nav>
        </Navbar.Collapse>
        </Navbar>
        
        <Route exact path="/home">
          <Home />
        </Route>
        <Route exact path="/about">
          <About />
        </Route>
        <Route exact path="/fish">
          <Fish />
        </Route>
        <Route exact path="/insects">
          <Insects />
        </Route>
        <Route exact path="/fossils">
          <Fossils />
        </Route>
        <Route exact path="/art">
          <Art />
        </Route>
      </div>
      </HashRouter>
  );
}

const Insects = () => <div><h2>Insects</h2></div>
const Fossils = () => <div><h2>Fossils</h2></div>
const Art = () => <div><h2>Art</h2></div>

export default App;
