import React from 'react';
import Table from 'react-bootstrap/Table';
import Image from 'react-bootstrap/Image';

class Fish extends React.Component {
    render() {
        return (
            <div>
                <h1>Fish</h1>
                <Table responsive="lg">
                    <thead>
                        <tr>
                            <th>Name</th>
                            <th>Image</th>
                            <th>Price</th>
                            <th>Location</th>
                            <th>Shadow size</th>
                            <th>Time</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td>Berling</td>
                            <td><Image src="https://static.wikia.nocookie.net/animalcrossing/images/e/ea/NH-Icon-bitterling.png/revision/latest/scale-to-width-down/64?cb=20200401003128" /></td>
                            <td>900</td>
                            <td>River</td>
                            <td>1</td>
                            <td>All day</td>
                        </tr>
                    </tbody>
                </Table>
            </div>
        )
        
    }
}

export default Fish;